export const AuthReducer = (initialState, action) => {
	switch (action.type) {
		case 'REQUEST_LOGIN':
			return {
				...initialState,
				loading: true,
			};

    case 'REQUEST_SIGNUP':
      return {
        ...initialState,
        loading: true,
      };

    case 'REQUEST_USEREXIST':
      return {
        ...initialState,
        userExist: true,
        loading: true,
      };

		case 'LOGIN_SUCCESS':
      console.log('LOGIN_SUCCESS: ', action);
			return {
				...initialState,
				user: action.payload.user,
				token: action.payload.token,
				loading: false,
			};
    case 'SIGNUP_SUCCESS':
      console.log('SIGNUP_SUCCESS: ', action);
      return {
        ...initialState,
        user: action.payload.user,
        token: action.payload.token,
        loading: false,
      };

		case 'LOGOUT':
			return {
				...initialState,
				user: '',
				token: '',
			};
      
		case 'LOGIN_ERROR':
			return {
				...initialState,
				loading: false,
				errorMessage: action.error,
			};

    case 'SIGNUP_ERROR':
      return {
        ...initialState,
        loading: false,
        errorMessage: action.error,
      };

    case 'USEREXIST_ERROR':
      return {
        ...initialState,
        userExist: false,
        errorMessage: action.error,
      };

		default:
			throw new Error(`Unhandled action type: ${action.type}`);
	}
};
